<template>
    <div class="withdrawal" :class="{showSearch:showSearch}">
		<div class="search-box">
		    <el-input :placeholder="$L('输入订单编号')" size="small" v-model="inputValue" class="input-with-select"
		              clearable
		              @keyup.enter.native="$event.target.blur"
		              @change="searchFunc">
		        <el-button slot="append" icon="el-icon-search"></el-button>
		    </el-input>
		    <div class="search-close" @click="doSearch(false)"><i class="el-icon-close"></i></div>
		</div>
        <vue-scroll>
            <div class="main-table">
                <div class="withdrawal-head" v-if="tableData">
                    <div class="top">
                        <p class="p" style="font-weight: 700;">{{ $L('分销商用户') }}：{{ tableData.username ? tableData.username : '-'}}</p>
                        <p class="p">{{ $L('姓名') }}：{{ tableData.contact ? tableData.contact : '-'}}</p>
                        <p class="p">{{ $L('手机') }}：{{ tableData.mobile ? tableData.mobile : '-'}}</p>
						<p class="p">{{ $L('分销商等级') }}：{{ tableData.grade ? tableData.grade : '-' }}</p>
						<p class="p" style="width: 100%;">{{ $L('成为分销商时间') }}：{{ tableData.createTime ? tableData.createTime : '-'}}</p>
                    </div>
                    <div class="main-box">
                        <div class="main-item">
                            <i class="bbx-font mainFontColor bbx-icon-weibiaoti--5"></i>
                            <div class="info">
                                <p class="p">{{ $L('分销订单总额') }}</p>
                                <p class="font-large">{{ tableData.totalAmount }}</p>
                            </div>
                        </div>
                        <div class="main-item">
                            <i class="bbx-font mainFontColor bbx-icon-weibiaoti--6"></i>
                            <div class="info">
                                <p class="p">{{ $L('佣金提成总额') }}</p>
                                <p class="font-large">{{ tableData.fullCommission }}</p>
                            </div>
                        </div>
                        <div class="main-item">
                            <i class="bbx-font mainFontColor bbx-icon-wodefenxiao"></i>
                            <div class="info">
                                <p class="p">{{ $L('下级分销商数') }}</p>
                                <p class="font-large">{{ tableData.subordinateCount }}</p>
                            </div>
                        </div>
                        <div class="main-item">
                            <i class="bbx-font mainFontColor bbx-icon-fenxiaozhongxin"></i>
                            <div class="info">
                                <p class="p">{{ $L('子会员数') }}</p>
                                <p class="font-large">{{ tableData.memberCount }}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="list">
                    <rank-tab :tab-list="theadName"></rank-tab>
					<div v-if="tableData.orderList && tableData.orderList.length > 0">
						<div class="table-box" v-for="item in tableData.orderList">
						    <div class="box">
						        <div class="top">
						            <span>{{$L('订单编号')}}:{{item.orderSn}}</span><span style="float: right;" :class="statusFunc(item.pendingStatus).color">{{ statusFunc(item.pendingStatus).text }}</span>
						        </div>
						        <div class="item" v-for="(i,k) in item.productList">
						            <div class="l-box">
						                <img class="img" :src="i.img">
						                <div class="msg">
						                    <p class="tit mainFontColor">{{ i.prodName }}</p>
						                </div>
						            </div>
						            <div class="r-box">
						                <p>{{i.goodsPrice}}</p>
						                <p>x{{i.goodsNum}}</p>
						            </div>
						        </div>
						        <div class="total">
						            <span>{{ $L('共')}}{{item.countNum}}{{ $L('件商品')}} {{ $L('合计')}}:{{item.orderAmount}}</span>
						        </div>
						    </div>
						    <div class="bottom">
								<span class="relation">{{ $L('关系')}}：{{ item.relation }}</span>
						        <span>{{ $L('下单用户')}}：{{item.username}}</span><br>
						        <span class="commission">{{ $L('我所得的佣金')}} <b class="font-red">{{item.commissionPrice}}</b></span>
						    </div>
						</div>
						<div class="list-pages" v-if="tableData.allNum > pageSize">
						    <el-pagination
						            small
						            :background="true"
						            @size-change="handdleSizeChange"
						            @current-change="handdleCurrentChange"
						            :current-page="currentPage"
						            :page-size="pageSize"
						            layout="prev, pager, next"
						            :total="tableData.allNum">
						    </el-pagination>
						</div>
					</div>
					<div class="no-data" v-else>
					    {{ $L('暂无数据') }}
					</div>
                </div>
            </div>
        </vue-scroll>
    </div>
</template>

<script>
	import  rankTab from "../../../components/common/rankTab.vue"

    export default {
        name: "withdrawal",
        data (){
            return {
				showSearch: false,
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                inputValue:'',
                theadName:{
                    type:'all',
                    list:[
                        {
                            text: this.$L('全部订单'),
                            type:'all',
                            func: () => {
                                if(this.$route.params.hasOwnProperty('id')){
                                    this.$router.push({params:{type:'all',id:this.$route.params.id}});
                                }else{
                                    this.$router.push({params:{type:'all'}});
                                }
                            }
                        },
                        {
                            text:this.$L('已结算'),
                            type:'settlement',
                            func: () => {
                                if(this.$route.params.hasOwnProperty('id')){
                                    this.$router.push({params:{type:'settlement',id:this.$route.params.id}});
                                }else{
                                    this.$router.push({params:{type:'settlement'}});
                                }
                            }
                        },
                        {
                            text:this.$L('未结算'),
                            type:'unSettlement',
                            func: () => {
                                if(this.$route.params.hasOwnProperty('id')){
                                    this.$router.push({params:{type:'unSettlement',id:this.$route.params.id}});
                                }else{
                                    this.$router.push({params:{type:'unSettlement'}});
                                }
                            }
                        }
                    ],
                },
                tableData: {},
                pageSize:10,
                currentPage:1,
                userId:'',
                sortBy:'',
            }
        },
        components : {
			rankTab
        },
		computed:{
		    mobileTopBtn: {
		        get() {
		            return this.$store.state.mobileTopBtn;
		        },
		        set(val) {
		            this.$store.commit('setMobileTopBtn', val);
		        }
		    }
		},
		beforeRouteUpdate (to, from, next) {
		    // 在当前路由改变，但是该组件被复用时调用
		    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
		    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
		    // 可以访问组件实例 `this`
			
		    this.currentPage = 1;
		    this.getData(to);
			this.makeTop();
		    next();
		},
        created () {
			
            this.getData();
			this.makeTop();
        },
		beforeDestroy() {
		    //销毁当前页面时，必须清空顶部按钮，不然会影响其他页面
		    this.mobileTopBtn = null
		},
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                _self.loading = true;
                if(!router){
                    router = _self.$route;
                };
                _self.theadName.type = router.params.hasOwnProperty('type') ? router.params.type : 'all';
                _self.userId = router.params.hasOwnProperty('id') ? router.params.id : '';
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    settlementStatus : _self.theadName.type,
                    keyword : _self.inputValue,
                    distributorId : _self.userId ,
                };
                _self.$api('distributorInfo', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
							_self.$router.push({name: 'distributorMain'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
						_self.$router.push({name: 'distributorMain'});
                    });
                });
            },
			makeTop(){
				this.mobileTopBtn = [
					{
						icon: 'bbx-icon-sousuo',
						func: () => {
							this.doSearch(true);
						}
					},
				];
			},
			doSearch(type){
			    if(type){
			        this.showSearch = true;
			    }else{
			        this.searchType = "all";
			        this.showSearch = false;
			    }
			},
            handdleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            handdleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            changeTypeFunc(val){
                this.type = val;
                this.getData();
            },
			searchFunc(val){
			    this.inputValue = val;
			    this.getData();
			},
			statusFunc(value){
			    let x;
			    switch(value){
			        case 0:
			            x = {text:this.$L('未结算'),color:'font-red'};
			            break;
			        default:
			            x = {text:this.$L('已结算'),color:'font-green'};
			            break;
			    }
			    return x;
			},
        },
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib.scss";
    .withdrawal{
        .search-box{
            position: absolute;
            width: 100%;
            height: 45px;
            line-height: 45px;
            background-color: #fff;
            top: -45px;
            left: 0;
            z-index: 100;
            border-bottom: 1px solid #E4E7ED;
            @include diy-animation-show(top);
            .input-with-select{
                width: 280px;
                margin-left: 10px;
            }
            .search-close{
                width: 45px;
                height: 45px;
                text-align: center;
                line-height: 45px;
                position: absolute;
                top: 0;
                right: 0;
                z-index: 100;
                font-size: 18px;
                color: #909399;
                &:active{
                    color: #606266;
                }
            }

        }
        .main-table{
            position: relative;
            z-index: 0;
            @include diy-animation-show(margin);
            .state{
				padding: 0 10px;
                height: 45px;
                line-height: 45px;
                margin-bottom: 10px;
                background-color: #fff;
                .el-select{
                    margin-left: 10px;
                    width: 100px;
                    vertical-align: bottom;
					.el-input__inner{
						line-height: 31px;
					}
                }
                .time-search{
                    width: 230px;
                }
                .commission{
                    display: inline-block;
                    width: 75px;
                    height: 32px;
                    color: #fff;
                    line-height: 32px;
                    text-align: center;
                    float: right;
                    margin-top: 7px;
                }
            }
            .withdrawal-head{
                position: relative;
                background: #fff;
                .top{
                    padding: 20px;
                    display: flex;
                    justify-content: flex-start;
                    flex-wrap: wrap;
                    border-bottom: 1px solid #ccc;
                    .p{
                        width: 50%;
                        margin-bottom: 10px;
                        &:last-child{
                            margin-bottom:0;
                        }
                    }
                }
                .main-box{
                    padding: 20px 10px 10px;
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    .main-item{
                        width: 50%;
                        margin-bottom: 10px;
                        .bbx-font{
                            font-size: 40px;
                            vertical-align: super;
                            margin-right: 10px;
                        }
                        .info{
							display: inline-block;
							width: calc(100% - 52px);
							overflow: hidden;
							text-overflow: ellipsis;
							white-space: nowrap;
                            .p {
                                font-size: 14px;
								overflow: hidden;
								text-overflow: ellipsis;
								white-space: nowrap;
                            }
                            .font-large{
                                font-size: 16px;
                                font-weight: 700;
								overflow: hidden;
								text-overflow: ellipsis;
								white-space: nowrap;
                            }
                        }
                    }
                }
            }
            .list{
                margin-top: 10px;
				.rank-tab{
					margin-bottom: 10px;
					margin-left: 10px;
				}	
				.table-box{
				    background-color: #fff;
				    margin-bottom: 10px;
				    width: 100%;
				    .box{
				        padding: 10px 10px 0;
				        .top{
				            height: 30px;
				            line-height: 30px;
				            margin-bottom: 10px;
				        }
				        .item{
				            overflow: hidden;
				            .l-box{
				                float: left;
				                width: 80%;
				                .img{
				                    width: 60px;
				                    height: 60px;
				                }
				                .msg{
				                    width: 70%;
				                    display: inline-block;
				                    vertical-align: top;
				                    padding-left: 10px;
				                    .tit{
				                        text-overflow: ellipsis;
				                        display: -webkit-box;
				                        -webkit-line-clamp: 2;
				                        -webkit-box-orient: vertical;
				                        overflow: hidden;
				                    }
				                }
				            }
				            .r-box{
				                float: right;
				                text-align: right;
				            }
				        }
				        .total{
				            text-align: right;
				            line-height: 40px;
				        }
				    }
				    .bottom{
				        padding: 10px;
				        text-align: right;
				        border-top: 1px solid #E1E1E1;
				        .commission{
				            padding-left: 30px;
				        }
						.relation{
						    padding-right: 30px;
						}
						span{
							line-height: 25px;
						}
				    }
				    .font-red {
				        color: #ff0000;
				    }
				    .font-green {
				        color: #00bf00;
				    }
				}
                .list-pages{
                    text-align: center;
                    padding: 20px 0;
                    background-color: #fff;
                }
                .currentClick {
                    background-color: #fff;
                }
				.no-data{
				    position: relative;
				    width: 100%;
				    height: 170px;
				    line-height: 170px;
				    text-align: center;
				    background-color: #ffffff;
				}
            }
			
        }

        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
        &.showSearch{
            .search-box{
                top: 0;
            }
            .main-table{
                margin-top: 45px;
            }
        }
    }

    @media screen and (max-width: 320px) {
        .main-box{
            .main-item{
                padding-left: 5px!important;
            }
        }
    }
</style>